import React, { Component } from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch, Link } from 'react-router-dom';
import { Carousel, WingBlank } from 'antd-mobile';
import Goods from './Goods';

export default class Home extends Component {
    state = {
        data: ['1', '2', '3'],
        imgHeight: 176,
    }
    componentDidMount() {
        setTimeout(() => {
          this.setState({
            data: ['AiyWuByWklrrUDlFignR', 'TekJlZRVCjLFexlOCuWn', 'IJOtIlfsYdTyaDTRVrLI'],
          });
        }, 100);
    }
    render() {
        return (
            <div style={{width: "360px"}}>
                <div style={{width: "100%",height: "50px",backgroundColor: "#ff852a"}}>
                    <Link to="/search">
                        <div style={{width: "70%",backgroundColor: "#ff4e22",height: "30px",position: "relative",top: "10px",left: "15%"}}>
                            <img src="/ss.png" style={{transform: "scale(0.1)",position: "relative",top: "-85px",left: "-5%"}}></img>
                            <p style={{fontSize: "10px",position: "relative",top: "-195px",marginLeft: "105px",color: "white"}}>寻找宝贝店铺</p>
                        </div>
                    </Link>
                </div>
                <WingBlank>
                    <Carousel
                    autoplay={false}
                    infinite
                    beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                    afterChange={index => console.log('slide to', index)}
                    >
                    {this.state.data.map(val => (
                        <a
                        key={val}
                        href="http://www.alipay.com"
                        style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                        >
                        <img
                            src={`https://zos.alipayobjects.com/rmsportal/${val}.png`}
                            alt=""
                            style={{ width: '100%', verticalAlign: 'top' }}
                            onLoad={() => {
                            window.dispatchEvent(new Event('resize'));
                            this.setState({ imgHeight: 'auto' });
                            }}
                        />
                        </a>
                    ))}
                    </Carousel>
                </WingBlank>
                <Goods></Goods>
            </div>
        )
    }
}


