import React, { Component } from 'react';
import Axios from 'axios' ;
import xinxi from "./xinxi";

export default class login extends Component {
    getData=()=>{
        Axios({
            method:"get",
            url:"https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login"
        }).then((res)=>{
            console.log(res)
            console.log(res.data.data.name);
            xinxi[0].userinfo = res.data.data.name;
            console.log(res.data.desc)
            xinxi[0].desc = res.data.desc;
        })
    }
    componentDidMount(){
        document.title = '登录';
        this.getData();    
    }
    render() {
        return (
            <div style={{marginTop:"90px",marginLeft:"6%"}}>
                <p style={{fontSize:"18px"}}>账号</p>
                <input type="text" style={{width:"92%",height:"30px",marginTop:"11px"}}></input>
                <p style={{fontSize:"18px",marginTop:"10px"}}>密码</p>
                <input type="text" style={{width:"92%",height:"30px",marginTop:"10px"}}></input>
                <button style={{display: "block",marginTop:"35px",width:"35%",height:"30px",marginLeft:"30%"}} onTouchEnd={()=>{this.jump()}}>登录</button>
            </div>
        )
    }
    jump=()=>{
        this.props.history.push('/')
    }
}
