import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import xinxi from './xinxi';

export default class GoodsDetail extends Component {
    constructor(){
        super();
        this.state = {
           data: [],
           sign: false,
           sc: "/sc.png"
        }
    }

    select=()=>{
        console.log(localStorage.getItem('id'))
        console.log(xinxi[0].data)
        for(var i=0;i<xinxi[0].data.length;i++){
            if(xinxi[0].data[i].id == localStorage.getItem('id')){
                console.log('true'+ +localStorage.getItem('id'))
                this.setState({
                    data: xinxi[0].data[i]
                },()=>{
                    console.log(this.state.data)
                })
            }else{
                console.log('false'+ +localStorage.getItem('id'))
            }
        }
    }
    
    componentDidMount(){
        document.title = '商品细节';
        this.select();
    }
    render() {
        return (
            <div>

                 <div style={{width: "100%",height: "40px",backgroundColor: "#ff852a"}}>
                    <Link to="/">
                        <img src="/fh.png" style={{transform: "scale(0.1)",position: "relative",top: "-80px",left: "-80px"}}></img>
                    </Link>
                </div>
                <div key={this.state.data.id} id={this.state.data.id}>
                    <img src={this.state.data.img} style={{transform: "scale(0.5)",marginLeft: "-22%",marginTop: "-90px"}}></img>
                    <p style={{fontSize: "18px",marginTop: "-110px",marginLeft: "10%"}}>{this.state.data.title}</p>
                    <p style={{fontSize:"15px",color:"red",marginTop:"-17px",marginLeft:"53%"}}>{this.state.data.price}<span style={{color:"black"}}>元</span></p>
                </div>
                {/*<img src="/1.jpg" style={{marginLeft: "30px",marginTop: "20px"}}></img>
                <p style={{marginTop: "20px",marginLeft: "30px",fontSize: "18px"}}><span style={{color: "red"}}>12.5</span>元/斤</p>
                <p style={{marginTop: "20px",marginLeft: "30px",marginRight: "50px",fontSize: "18px",fontFamily: "cursive",color: "orange"}}>应季的大橘子，父亲都抵挡不住的美味。“你在此处不要走动，我去给你买些橘子，我就吃两斤，剩下的全给你”</p> */}
                <div style={{width: "100%",height: "50px",borderTop: "3px solid #cdcdcd",marginTop: "285px",}}>
                    <div style={{height: "50px",marginTop: "-80px"}}>
                        <img src="/dp.png" style={{transform: "scale(0.15)",marginTop: "-5px",marginLeft: "-75px"}}></img>
                        <p style={{marginLeft: "10px",marginTop: "-88px",color: "#cdcdcd"}}>店铺</p>
                    </div>
                    <div style={{height: "50px",marginTop: "-50px",marginLeft: "50px"}}>
                        <img src="/kf.png" style={{transform: "scale(0.15)",marginTop: "-5px",marginLeft: "-75px"}}></img>
                        <p style={{marginLeft: "10px",marginTop: "-88px",color: "#cdcdcd"}}>客服</p>
                    </div>
                    <div style={{height: "50px",marginTop: "-50px",marginLeft: "100px"}}>
                        <img src= {this.state.sc} style={{transform: "scale(0.15)",marginTop: "-5px",marginLeft: "-75px"}} onTouchEnd = {()=>{this.collection()}}></img>
                        <p style={{marginLeft: "10px",marginTop: "-88px",color: "#cdcdcd"}}>收藏</p>
                    </div>
                </div>
                <button style={{width: "100px",height: "35px",backgroundColor: "#ff9a01",position: "relative",top: "-42px",left: "145px",color: "white",border: "none",borderTopLeftRadius: "17px",borderBottomLeftRadius: "17px"}}>加入购物车</button>
                <button style={{width: "100px",height: "35px",backgroundColor: "#fe5a08",position: "relative",top: "-42px",left: "145px",color: "white",border: "none",borderTopRightRadius: "17px",borderBottomRightRadius: "17px"}}>立即购买</button>
            </div>
        )
    }
    collection=()=>{
        if(xinxi[0].userinfo == "1"){
            console.log("请登录")
            this.props.history.push('/login')
        }else{
            xinxi[0].shoucang.push(localStorage.getItem('id'));
            console.log(xinxi[0].shoucang);
            if(this.state.sign == false){
                this.setState({
                    sign: true,
                    sc: "/sc1.png"
                })
            }else{
                this.setState({
                    sign: false,
                    sc:"/sc.png"
                })
            }
        }
    }
}
