import React, { Component } from 'react';
import Axios from 'axios' ;
import xinxi from './xinxi.js';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch, Link } from 'react-router-dom';

export default class Goods extends Component {
    constructor(){
        super();
        this.state = {
            data : [],
            shoucang : [],
            userinfo : "",
        }
    }

    getData = ()=>{
        Axios({
            method:"get",
            url:"https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist"
        }).then((res)=>{
            this.setState({
                data : res.data.data
            },()=>{
                console.log(this.state.data)
                xinxi[0].data = this.state.data;
                console.log(xinxi[0].data)
            })
        })
    }

    componentDidMount(){
        document.title = '首页';
        this.getData();
        
    }

    render() {
        var temp = this.state.data.map((item,index)=>(
        <Link to="/goodsDetail">
            <div key={item.id} id={item.id} style={{width: '50%',height: '200px',display: (index%2 == 0)?"":"inline"}} onTouchEnd = {(e)=>{this.goTo(e)}}>
                <img src={item.img} id={item.id} style={{transform: "scale(0.3)",marginTop: (index%2 == 0)?"-160px":"-360px",marginLeft: (index%2 == 0)?"-89%":"0%"}}></img>
                <p style={{color: "black",fontSize: "18px",marginTop: "-170px",marginLeft: (index%2 == 0)?"12px":"173px"}}>{item.title}</p>
                <p style={{marginTop: "-15px",marginLeft: (index%2 == 0)?"125px":"285px",color: 'red'}}>{item.price}<span style={{color: "black"}}>元</span></p>
            </div>
        </Link>
        ))

        return (
            <div>
                {temp}
            </div>
        )
    }

    goTo = (e)=>{
        console.log(e.target.id)
        localStorage.setItem('id',e.target.id)
    }
}
