import { data } from 'jquery';
import React, { Component } from 'react';
import xinxi from "./xinxi"

export default class Cart extends Component {
    constructor(){
        super();
        this.state = {
            data:[]
        }
    }
    shaixuan=()=>{
        console.log(xinxi[0].data);
        console.log(xinxi[0].shoucang);
        for(var i=0;i<xinxi[0].shoucang.length;i++){
            console.log(i)
            for(var j=0;j<xinxi[0].data.length;j++){
                console.log(j)
                if(xinxi[0].data[i].id == xinxi[0].shoucang[j]){
                    this.state.data.push(xinxi[0].data[i])
                    console.log(this.state.data)
                }
            }
        }
    }
    componentDidMount(){
        document.title = '收藏';
        this.shaixuan();
    }
    render() {
        var temp = this.state.data.map((item,index)=>(
            <div key={item.id} id={item.id}>
                <div>
                    {item.title}
                </div>
                <button id={item.id} onTouchEnd = {(e)=>{this.del(e)}}>删除</button>
            </div>
        ))
        return (
            <div>
                {temp}
            </div>
        )
    }
    del=(e)=>{
        const arr = this.state.data;
        arr.splice(e.tardet.id,1);
        this.setState({
            data: arr
        })
    }
}

